  grid-template-columns: 1fr 1fr;
  max-width: 585px;
  gap: 15px 36px;
  padding-top: 14px;
}
@media (max-width: 1023px) {
  .form form {
    max-width: 670px;
    gap: 20px 30px;
    padding-top: 0;
  }
}
@media (max-width: 767px) {
  .form form {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
}
.form h2 {
  display: block;
  font-family: "Roboto Condensed", "Helvetica", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  color: #ffffff;
  margin: 0;
  grid-column: 1/3;
  padding-bottom: 16px;
}
@media (max-width: 1023px) {
  .form h2 {
    padding-bottom: 7px;
  }
}
@media (max-width: 767px) {
  .form h2 {
    font-size: 30px;
    line-height: 140.62%;
  }
}
.form p {
  display: block;
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  margin: 0;
  padding-bottom: 21px;
  grid-column: 1/3;
}
@media (max-width: 1023px) {
  .form p {
    padding-bottom: 25px;
  }
}
@media (max-width: 767px) {
  .form p {
    font-size: 14px;
    line-height: 134.4%;
  }
}
.form__name--modal {
  margin-top: 20px;
}
.form__tel--modal {
  margin-top: 10px;
}
.form__question--modal {
  padding-bottom: 10px;
}
.form__background {
  margin-right: 49px;
  margin-top: 85px;
}
@media (max-width: 1023px) {
  .form__background {
    display: none;
    margin: 0;
  }
}
.form__button {
  display: block;
  padding: 13px 25px;
  color: #ffffff;
  background-color: #041827;
  border: none;
  border-radius: 3px;
  font-family: "Roboto Condensed", "Helvetica", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  width: 100%;
  cursor: pointer;
}
@media (max-width: 1023px) {
  .form__button {
    max-width: 265px;
  }
}
@media (max-width: 767px) {
  .form__button {
    max-width: 270px;
    margin-top: 0px;
  }
}
.form__button:hover {
  box-shadow: 0 0 10px 10px rgba(136, 139, 141, 0.5);
  color: rgba(255, 255, 255, 0.7);
}
.form__button.focus-visible:focus {
  box-shadow: 0 0 10px 10px rgba(136, 139, 141, 0.5);
  color: rgba(255, 255, 255, 0.7);
}
.form__button:active {
  background-color: rgba(22, 22, 22, 0.8);
  box-shadow: 0 0 10px 10px rgba(136, 139, 141, 0.5);
}

.form__name input,
.form__tel input {
  padding: 15px;
  background-color: rgba(4, 20, 39, 0.15);
  color: #ffffff;
  border-radius: 3px;
  border: none;
  min-width: 275px;
  cursor: pointer;
}
.form__name input:not(:disabled):hover, .form__name input:not(:disabled):focus,
.form__tel input:not(:disabled):hover,
.form__tel input:not(:disabled):focus {
  background-color: rgba(4, 20, 39, 0.5);
  box-shadow: 0 0 10px 10px rgba(136, 139, 141, 0.5);
}
.form__name input:active::placeholder,
.form__tel input:active::placeholder {
  color: #ffffff;
}
@media (max-width: 1023px) {
  .form__name input,
.form__tel input {
    min-width: 320px;
  }
}
@media (max-width: 767px) {
  .form__name input,
.form__tel input {
    min-width: 270px;
    width: 100%;
    font-size: 16px;
    line-height: 22px;
  }
}
.form__name input::placeholder,
.form__tel input::placeholder {
  color: #ffffff;
}

.form__question {
  grid-column: 1/3;
  margin-bottom: 20px;
}
@media (max-width: 1023px) {
  .form__question {
    padding-bottom: 8px;
    margin-bottom: 0;
  }
}
@media (max-width: 767px) {
  .form__question {
    padding-bottom: 4px;
  }
}
.form__question textarea {
  background-color: rgba(4, 20, 39, 0.15);
  border: none;
  border-radius: 3px;
  color: #ffffff;
  padding: 25px 0 15px 15px;
  width: 100%;
  min-height: 155px;
  cursor: pointer;
}
.form__question textarea:not(:disabled):hover, .form__question textarea:not(:disabled):focus {
  background-color: rgba(4, 20, 39, 0.5);
  box-shadow: 0 0 10px 10px rgba(136, 139, 141, 0.5);
}
.form__question textarea:active::placeholder {
  color: #ffffff;
}
@media (max-width: 767px) {
  .form__question textarea {
    min-width: 270px;
    min-height: 182px;
  }
}
.form__question textarea::placeholder {
  color: #ffffff;
  font-size: 16px;
  line-height: 22px;
}
.form__question--modal {
  margin-bottom: 0;
}

.form__checkbox {
  max-width: 234px;
}
.form__checkbox label {
  position: relative;
  display: block;
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  cursor: pointer;
  margin-left: 36px;
  margin-top: 16px;
}
@media (max-width: 1023px) {
  .form__checkbox label {
    margin-left: 26px;
    margin-top: 10px;
  }
}
@media (max-width: 767px) {
  .form__checkbox label {
    margin-left: 31px;
    margin-top: 6px;
  }
}
.form__checkbox input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
.form__checkbox span {
  opacity: 0.8;
}
.form__checkbox span::before {
  position: absolute;
  content: "";
  width: 15px;
  height: 15px;
  border: 1px solid #ffffff;
  border-radius: 2px;
  left: -26px;
  top: 5px;
}
.form__checkbox input[type=checkbox]:checked + span::after {
  position: absolute;
  content: "";
  width: 7px;
  height: 7px;
  background-image: url("../img/svg/checkmark.svg");
  background-repeat: no-repeat;
  left: -24px;
  top: 10px;
}
.form__checkbox input:hover + span::before, .form__checkbox input:focus + span::before {
  border: 1px solid #2b2c2c;
}
.form__checkbox input:hover + span, .form__checkbox input:focus + span {
  color: #2b2c2c;
}

.form__checkbox--modal {
  max-width: 100%;
}
.form__checkbox--modal label {
  margin-left: 34px;
  margin-top: 0px;
}

.form-modal {
  max-height: 610px;
  padding: 0;
}

.main-wrapper {
  width: 100%;
  background-color: #00b3ff;
  background-image: linear-gradient(139deg, rgba(4, 24, 39, 0.5) 33.7%, #00b3ff 33.7% 100%);
}
@media (max-width: 1023px) {
  .main-wrapper {
    background-image: none;
  }
}
@media (max-width: 767px) {
  .main-wrapper {
    padding-top: 0;
    padding-bottom: 0;
  }
}

.footer {
  background-color: #041827;
}

.footer__wrapper {
  display: grid;
  grid-template-columns: 300px 1fr;
  padding: 50px 110px;
  max-width: 1440px;
  margin: 0 auto;
  gap: 60px;
}
@media (max-width: 1023px) {
  .footer__wrapper {
    grid-template-columns: 1fr;
    gap: 0;
  }
}
@media (max-width: 767px) {
  .footer__wrapper {
    display: inline-block;
    padding: 0;
    margin: 0 auto;
  }
}

.footer__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.footer__item {
  padding-bottom: 10px;
}

.footer__item a {
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 135%;
  color: rgba(255, 255, 255, 0.7);
}
@media (max-width: 1023px) {
  .footer__item a {
    line-height: 156%;
  }
}
@media (max-width: 767px) {
  .footer__item a {
    line-height: 107%;
  }
}

.footer__adress {
  margin: 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .footer__adress {
    display: flex;
    flex-direction: column;
    padding-left: 25px;
  }
}

.footer__contacts {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.footer__item p {
  margin: 0;
  color: rgba(255, 255, 255, 0.7);
  line-height: 135%;
  width: 345px;
}
@media (max-width: 1023px) {
  .footer__item p {
    width: 100%;
  }
}

@media (max-width: 1023px) {
  .footer__nav {
    grid-column: 1/2;
  }
}

@media (max-width: 1023px) {
  .social {
    display: flex;
    justify-content: space-between;
    padding-bottom: 36px;
    max-width: 1440px;
  }
}
@media (max-width: 1023px) {
  .social {
    grid-column: 1/3;
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding-top: 50px;
    padding-bottom: 36px;
  }
}
@media (max-width: 767px) {
  .social {
    flex-direction: column;
    align-items: center;
    padding: 34px 15px 20px;
  }
}

.social__list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style-type: none;
  padding-top: 34px;
  flex-wrap: wrap;
}
@media (max-width: 1023px) {
  .social__list {
    padding: 0;
    align-items: center;
  }
}
@media (max-width: 767px) {
  .social__list {
    margin-top: 21px;
    margin-right: 20px;
  }
}

.social__icon {
  fill: #f8fdff;
}
.social__icon:hover {
  fill: #00b2ff;
}
.social__icon.focus-visible:focus {
  fill: #00b2ff;
}
.social__icon:active {
  fill: #006eff;
}

.social__item {
  margin-left: 15px;
}
@media (max-width: 767px) {
  .social__item {
    margin-left: 25px;
  }
}
.social__item--vk {
  margin-left: 25px;
}
.social__item--pinterest {
  margin-left: 25px;
}

.social__logo:hover {
  opacity: 0.8;
}
.social__logo.focus-visible:focus {
  opacity: 0.8;
}
.social__logo:active {
  opacity: 0.6;
}

[data-accordion=element].is-active > [data-accordion=content] {
  max-height: 100%;
}

[data-accordion=parent].no-transition [data-accordion=content] {
  transition: none;
}

[data-accordion=content] {
  max-height: 0;
  transition: max-height 0.3s;
  overflow: hidden;
}
@media (max-width: 1919px) {
  [data-accordion=content] {
    transition: max-height 0.3s;
    overflow: hidden;
  }
}
@media (max-width: 767px) {
  [data-accordion=content] {
    max-height: 0;
    transition: max-height 0.3s;
    overflow: hidden;
  }
}

.accordion {
  display: grid;
  margin-top: 7px;
  margin-left: 14px;
}
@media (max-width: 1919px) {
  .accordion {
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 100%;
    gap: 20px;
    margin: 0;
    padding-bottom: 55px;
  }
}
@media (max-width: 767px) {
  .accordion {
    display: block;
    max-width: 767px;
    padding: 0;
    margin: 0 auto 50px auto;
  }
}
.accordion__link:hover {
  opacity: 0.7;
}
.accordion__link.focus-visible:focus {
  opacity: 0.7;
}
.accordion__link:active {
  opacity: 0.5;
}
@media (max-width: 767px) {
  .accordion .accordion {
    width: 90%;
    margin: 0 auto;
  }
}
.accordion__content {
  display: grid;
  overflow: visible;
  min-height: 180px;
  min-width: 406px;
}
@media (max-width: 1919px) {
  .accordion__content {
    min-width: 100%;
    display: flex;
    flex-direction: column;
    min-height: 233px;
  }
}
@media (max-width: 767px) {
  .accordion__content {
    display: block;
    min-height: 0;
    overflow: hidden;
  }
}
.accordion__element--adress {
  grid-column: 2/3;
  margin-left: 95px;
}
@media (max-width: 1919px) {
  .accordion__element--adress {
    margin-left: 0;
  }
  .accordion__element--adress h3 {
    padding-bottom: 24px;
    margin-left: 10px;
  }
}
@media (max-width: 767px) {
  .accordion__element--adress h3 {
    margin-left: 0;
  }
}
@media (max-width: 767px) {
  .accordion__element:last-child {
    margin-bottom: 0;
  }
}
@media (max-width: 767px) {
  .accordion__element.is-active {
    padding: 11px 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.7);
  }
}
@media (max-width: 767px) {
  .accordion__element.is-active > .accordion__button {
    opacity: 0.7;
    border-bottom: none;
  }
  .accordion__element.is-active > .accordion__button::before, .accordion__element.is-active > .accordion__button::after {
    transform: rotate(540deg);
    transition: all 1s ease-out;
  }
}
.accordion__element h3 {
  color: #ffffff;
  font-family: "Roboto Condensed", "Helvetica", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  margin: 0;
  padding-bottom: 20px;
}
@media (max-width: 767px) {
  .accordion__element h3 {
    position: absolute;
    left: 24px;
    padding-top: 25px;
  }
}
.accordion__button {
  display: none;
}
@media (max-width: 767px) {
  .accordion__button {
    display: inline-block;
    position: relative;
    width: 100%;
    padding: 25px 25px;
    border: 1px solid rgba(255, 255, 255, 0.7);
    border-left: none;
    border-right: none;
    background-color: transparent;
    text-transform: uppercase;
    color: #ffffff;
    cursor: pointer;
    text-align: left;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 107%;
    letter-spacing: 0.05em;
  }
  .accordion__button--adress {
    padding-bottom: 30px;
  }
  .accordion__button::after, .accordion__button::before {
    position: absolute;
    content: "";
    width: 15px;
    height: 1px;
    right: 26px;
    bottom: 21px;
    background-color: #ffffff;
  }
  .accordion__button::before {
    transform: rotate(90deg);
  }
  .accordion__button--active {
    border-bottom: none;
  }
}
.accordion__list {
  padding: 0;
  list-style-type: none;
  margin: 0;
  max-width: 220px;
}
@media (max-width: 767px) {
  .accordion__list {
    margin-left: 25px;
  }
}
.accordion__list--second {
  grid-column: 2/2;
}
.accordion__list--contacts {
  min-width: 330px;
}
@media (max-width: 1919px) {
  .accordion__list--contacts {
    padding-left: 10px;
  }
}
@media (max-width: 767px) {
  .accordion__list--contacts {
    padding-left: 0;
  }
}
.accordion__item p {
  color: rgba(255, 255, 255, 0.7);
  padding-bottom: 9px;
  margin: 0;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 135%;
}
@media (max-width: 767px) {
  .accordion__item p {
    line-height: 135%;
    padding-bottom: 17px;
    max-width: 279px;
    font-size: 14px;
  }
}
.accordion__item a {
  color: rgba(255, 255, 255, 0.7);
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 156%;
  padding-bottom: 4.5px;
  display: block;
}
@media (max-width: 767px) {
  .accordion__item a {
    font-weight: 400;
    font-size: 14px;
    line-height: 179%;
  }
}

