// Colors
// ---------------------------------

// Default

$color-default-black: #000000;
$color-default-white: #ffffff;
$color-transparent: rgba(255, 255, 255, 0);
$color-white-transparent: rgba(255, 255, 255, 0.7);

// Project palette
// для нейминга цветов используем https://www.htmlcsscolor.com/hex/334482

$color-neon-blue: #2c39f2;
$color-torch-red: #ff1553;
$color-alice-blue: #f2f5f6;
$color-alice-blue-lightest: #f2f5f7;
$color-alice-blue-light: #f8fdff;
$color-whisper: #ebebeb;
$color-whisper-darker: #e5e5e5;
$color-deep-sky-blue: #00b2ff;
$color-deep-sky-blue-two: #00b3ff;
$color-deep-sky-blue-light: #53d9fab6;
$color-deep-sky-blue-lightest: rgba(0, 178, 255, 0.5);
$color-deep-sky-blue-dark: #006eff;
$color-deep-pearl: #041827;
$color-deep-pearl-darker: rgba(4, 20, 39, 0.7);
$color-deep-pearl-darkerer: rgba(4, 20, 39, 0.5);
$color-deep-pearl-transparent: rgba(4, 20, 39, 0.15);
$color-aluminium: #2b2c2c;
$color-aluminium-transparent: rgba(22, 22, 22, 0.8);

// Gradient

$black-to-right: linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);
$box-shadow-dark: 0 0 10px 10px rgba(136, 139, 141, 0.5);
$box-shadow-light: 0 0 10px 10px rgba(136, 139, 141, 0.5);
// Typography
// ---------------------------------

// переменная используемая в html для подключения скейлинга
$fz-default: 16px;

// у некоторых шрифтов в ios возникают проблемы с символом рубля
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-rouble: "Rouble", "Arial", sans-serif;

$ff-placeholder: "Placeholder", "Arial", sans-serif;

$ff-roboto-condensed: "Roboto Condensed", "Helvetica", sans-serif;

$ff-mono: "PT Mono", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default: ease;
$trans-default: 0.3s $tf-default;

// если требуется изинг отличный от $tf-default, то переменную называем не цифрой
$trans-modal: 0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600: 0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320: 320px;
$vp-375: 375px;
$vp-768: 768px;
$vp-1024: 1024px;
$vp-1280: 1280px;
$vp-1440: 1440px;
$vp-1920: 1920px;

// Retina
// ---------------------------------

$retina-dpi: 144dpi;
$retina-dppx: 1.5dppx;
